@import "/src/common/styles/sass/variables";

.projectsTitle {
  margin-bottom: 30px;

  .title {
    color: $textColor;
    font-weight: 600;
    font-size: 36px;
    text-align: center;
    line-height: 94px;
    height: 150px;
    margin: 0;
    @media (max-width: 872px) {
      height: 110px;
    }

    @media (max-width: 311px) {
      font-size: 32px;
    }

    @media (max-width: 277px) {
      line-height: 2;
    }


    .separatorLine {
      display: block;
      text-align: center;
      margin: 0 auto;
      background-color: #20c997;
      height: 3px;
      width: 100px;
    }

  }
}



